export default {
	data() {
		return {
			textList: ['财源滚滚', '步步高升', '一夜暴富', '每赌必赢', '逢凶化吉', '业绩长虹', '生意兴隆', '贵人相助'],
			selIdList: [],
			hasToken: false
		}
	},
	onLoad() {

	},
	onShow() {
		this.hasToken = uni.getStorageSync('first') ? true : false
	},
	methods: {
		goMatch() {
			try {
				let wishList = []
				if (this.selIdList.length === 3) {
					this.selIdList.forEach((item) => {
						wishList.push(this.textList[item])
					})
				} else {
					uni.showToast({
						title: "福报无穷，可求三卦",
						icon: 'none'
					})
					return
				}
				console.log(wishList)
				uni.setStorage({
					key: 'wishList',
					data: wishList,
					success: function() {
						uni.navigateTo({
							url: '/pages/birthday/birthday'
						});
					}
				});
			} catch (e) {
				// error
			}
		},
		getUserInfo(info) {
			console.log(info);
			if (info.detail.userInfo) {
			  console.log("点击了同意授权");
			  this.$api.post('/api/user/get_user_info',{
			  	user_id:uni.getStorageSync('user_id'),
			  	avatar_url: info.detail.userInfo.avatarUrl,
			  	nickname: info.detail.userInfo.nickName,
			  }).then((res)=>{
			  	console.log(res)
			  	
			
			  	this.goMatch()
			  	
			  })
			} else {
			  console.log("点击了拒绝授权");
			  this.back()
			}
		},
		selItem(index) {
			let i = this.selIdList.indexOf(index)
			if (i > -1) {
				this.selIdList.splice(i, 1)
			} else {
				if (this.selIdList.length >= 3) {
					this.selIdList.shift()
					this.selIdList.push(index)
				} else {
					this.selIdList.push(index)
				}
			}
		}
	}
}
